.scroll_navi {
  position: fixed;
  top: 50%;
  transform: translateY(-50%);
  right: 50px;
  z-index: 999;

  li {
    width: 12px;
    height: 12px;
    border: 1px solid #111;
    background: #fff;
    margin: 20px;
    cursor: pointer;
    transition: 0.5s;

    &.on {
      height: 100px;
      background: #111;
      border: 1px solid #fff;
    }
  }
}
