#pics {
  width: 100%;
  height: 100vh;
  background: lightgreen;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 10vw;
  position: relative;
  overflow: hidden;

  p {
    font: bold 200px/1 "arial";
    color: rgba(0, 0, 0, 0.3);
    position: absolute;
    top: 100px;
    left: 0;
  }

  h3 {
    font: bold 160px/1 "arial";
    color: #fff;
    position: absolute;
    top: 160px;
    left: 100px;
    text-shadow: 20px 20px 50px rgba(0, 0, 0, 0.4);
  }
}
