#news {
  width: 100%;
  height: 100vh;
  text-align: center;

  h2 {
    margin: 20px 0;
  }

  .content {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    perspective: 600px;

    article {
      width: 15%;
      background: #fff;
      min-height: 180px;
      border-radius: 40px;
      padding: 20px;
      margin: 10px;
      box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.1);
      transform: translateY(100px) rotateY(180deg);
      opacity: 0;
      transition-duration: 0.5s;

      @for $i from 0 to 4 {
        &:nth-of-type(#{$i + 1}) {
          transition-delay: 0.2s * $i;
        }
      }

      h2 {
        font: 16px/1 "arial";
        color: #ccc;
        margin-bottom: 20px;
      }

      p {
        font: 12px/1 "arial";
        color: #777;
      }
    }
  }

  &.on {
    article {
      opacity: 1;
      transform: translateY(0px) rotateY(0);
    }
  }
}
