#visual {
  width: 100%;
  height: 100vh;

  #slider {
    width: 100%;
    height: 100%;
    position: relative;
    overflow: hidden;

    .panel {
      width: 100%;
      height: 100%;
      position: relative;

      > li {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        display: none;

        &.on {
          display: flex;
        }

        &.s1 {
          background: rgba(223, 111, 180, 0.692);
        }
        &.s2 {
          background: rgb(58, 58, 46);
        }

        &.s3 {
          background: salmon;
        }

        &.s4 {
          background: seagreen;
        }
        &.s5 {
          background: rgb(18, 161, 154);
        }

        span {
          font-size: 50px;
          color: #fff;
        }
      }
    }

    .navi {
      position: absolute;
      bottom: 10vh;
      left: 10vw;

      li {
        float: left;
        margin-right: 15px;
        height: 4px;
        width: 50px;
        background: #fff;
        cursor: pointer;
        transition: 0.5s;

        &.on {
          background: #222;
          width: 150px;
        }
      }
    }

    .prev {
      width: 50px;
      height: 50px;
      background: #fff;
      position: absolute;
      bottom: 10vh;
      right: calc(5vw + 70px);
      border: none;
      cursor: pointer;
      text-transform: uppercase;
    }
    .next {
      text-transform: uppercase;
      position: absolute;
      bottom: 10vh;
      right: 5vw;
      width: 50px;
      height: 50px;
      background: #fff;
      border: none;
      cursor: pointer;
    }
  }
}
