.Gallery {
  .loading {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    z-index: 0;
  }
  figure {
    background: lightskyblue;
  }

  article {
    transform: transalteY(100px);
    opacity: 0;
    transition: 1s;

    &.on {
      opacity: 1;
      transform: translateY(0);
    }
    ul {
      &::after {
        content: "";
        display: block;
        clear: both;
      }

      li {
        width: 25%;
        float: left;
        padding: 10px;

        > .inner {
          width: 100%;
          background: #fff;
          padding: 10px;
          box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);

          .pic {
            width: 100%;

            img {
              width: 100%;
            }
          }

          h2 {
            font: normal 14px/1.2 "arial";
            color: #777;
            margin-top: 10px;
            margin-bottom: 20px;
          }

          .profile {
            display: flex;
            align-items: center;

            img {
              width: 40px;
              border-radius: 50%;
              margin-right: 10px;
            }

            span {
              font: 12px/1 "arial";
              color: #666;
              cursor: pointer;
              transition: 0.5s;
              font-weight: 600;

              &:hover {
                color: sandybrown;
                font-weight: 600;
              }
            }
          }
        }
      }
    }
  }
}

@media screen and (max-width: 1179px) {
  .Gallery article ul li {
    width: 33.3333%;
  }
}

@media screen and (max-width: 779px) {
  .Gallery article ul li {
    width: 50%;
  }
}

@media screen and (max-width: 639px) {
  .Gallery article ul li {
    width: 100%;
  }
}
